import React, { Fragment, useEffect, useState } from "react";
import { Card, Row, Col, Divider, List } from "antd";
import * as data from "../database/projects.json";
const { Meta } = Card;
export const Projects = () => {
  const [projects, setProjects] = useState(null);
  useEffect(() => {
    getData();
  });
  const getData = () => {
    setProjects(data.default);
  };
  return (
    <Fragment>
      <div>
        <Divider orientation="left">Projects</Divider>
        <Row gutter={{ xs: 8, sm: 16, md: 12, xl: 12 }}>
          {projects &&
            projects.map((x) => {
              return (
                <Col className="gutter-row" sm={{ span: 24 }} md={{ span: 12 }} xxl={{ span: 6 }} key={x.name}>
                  <div>
                    <Card
                      style={{
                        width: 300,
                        height: 450,
                        position: "relative",
                        marginBottom: "10px",
                      }}
                      cover={
                        <div style={{ height: "170px" }}>
                          <img
                            style={{
                              position: "relative",
                              top: "50%",
                              display: "block",
                              margin: "auto",
                              padding: "50px",
                              maxHeight: "250px",
                              transform: "translateY(-50%)",
                            }}
                            alt={x.name}
                            src={x.image}
                          />
                        </div>
                      }
                    >
                      <Divider>
                        <div style={{ fontSize: "15px" }}>{x.name}</div>
                      </Divider>
                      <p>
                        <b>Project Challenge:</b>
                      </p>
                      <List dataSource={x.challenges} renderItem={(item) => <p>- {item}</p>} />
                      <Meta style={{ position: "absolute", bottom: 10 }} title={x.company} description={x.date} />
                    </Card>
                  </div>
                </Col>
              );
            })}
        </Row>
      </div>
    </Fragment>
  );
};
